'use client';

import { useState, useEffect } from 'react';
import { Menu, X, Dumbbell } from 'lucide-react';

export default function Header() {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [activeSection, setActiveSection] = useState('inicio');
  const [isScrolled, setIsScrolled] = useState(false);

  const menuItems = [
    { id: 'inicio', label: 'In√≠cio', href: '#inicio' },
    { id: 'sobre', label: 'Sobre', href: '#sobre' },
    { id: 'treinos', label: 'Treinos', href: '#treinos' },
    { id: 'resultados', label: 'Resultados', href: '#resultados' },
    { id: 'diferenciais', label: 'Diferenciais', href: '#diferenciais' },
    { id: 'depoimentos', label: 'Depoimentos', href: '#depoimentos' },
    { id: 'contato', label: 'Contato', href: '#contato' }
  ];

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 50);
      
      // Detectar se√ß√£o ativa
      const sections = menuItems.map(item => item.id);
      const currentSection = sections.find(section => {
        const element = document.getElementById(section);
        if (element) {
          const rect = element.getBoundingClientRect();
          return rect.top <= 100 && rect.bottom >= 100;
        }
        return false;
      });
      
      if (currentSection) {
        setActiveSection(currentSection);
      }
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  const scrollToSection = (href: string) => {
    const element = document.querySelector(href);
    if (element) {
      element.scrollIntoView({ behavior: 'smooth' });
    }
    setIsMenuOpen(false);
  };

  return (
    <header 
      className={`fixed top-0 left-0 right-0 z-50 transition-all duration-500 ${
        isScrolled 
          ? 'glass-effect shadow-2xl backdrop-blur-xl' 
          : 'bg-transparent'
      }`}
    >
      <div className="container mx-auto px-4">
        <div className="flex items-center justify-between h-20">
          {/* Logo/Brand */}
          <div className="flex items-center space-x-3 group cursor-pointer">
            <div className="relative">
              <div className="w-12 h-12 gradient-bg-primary rounded-2xl flex items-center justify-center transform transition-all duration-300 group-hover:scale-110 group-hover:rotate-12">
                <Dumbbell className="w-6 h-6 text-white" />
              </div>
              <div className="absolute inset-0 w-12 h-12 gradient-bg-primary rounded-2xl opacity-30 blur-lg animate-glow-pulse"></div>
            </div>
            <div className="hidden sm:block">
              <h1 className="font-orbitron text-2xl font-bold text-gray-900">
                RICARDO
                <span className="gradient-text"> TEAM</span>
              </h1>
              <p className="text-xs text-gray-600 font-medium tracking-wider">PERSONAL TRAINER</p>
            </div>
          </div>

          {/* Desktop Navigation */}
          <nav className="hidden lg:flex items-center space-x-1">
            {menuItems.map((item) => (
              <button
                key={item.id}
                onClick={() => scrollToSection(item.href)}
                className={`relative px-6 py-3 rounded-full font-medium transition-all duration-300 group ${
                  activeSection === item.id
                    ? 'text-white'
                    : 'text-gray-700 hover:text-gray-900'
                }`}
              >
                {/* Active background */}
                {activeSection === item.id && (
                  <div className="absolute inset-0 gradient-bg-primary rounded-full animate-scale-in"></div>
                )}
                
                {/* Hover effect */}
                <div className="absolute inset-0 bg-gray-100 rounded-full opacity-0 group-hover:opacity-100 transition-opacity duration-300"></div>
                
                <span className="relative z-10">{item.label}</span>
                
                {/* Active indicator */}
                {activeSection === item.id && (
                  <div className="absolute -bottom-1 left-1/2 transform -translate-x-1/2 w-2 h-2 gradient-bg-primary rounded-full animate-scale-in"></div>
                )}
              </button>
            ))}
          </nav>

          {/* CTA Button */}
          <div className="hidden lg:block">
            <button
              onClick={() => scrollToSection('#contato')}
              className="btn-modern text-white font-semibold"
            >
              Come√ßar Agora
            </button>
          </div>

          {/* Mobile Menu Button */}
          <button
            onClick={() => setIsMenuOpen(!isMenuOpen)}
            className="lg:hidden relative w-12 h-12 rounded-2xl glass-effect flex items-center justify-center transition-all duration-300 hover:scale-110"
          >
            <div className="relative w-6 h-6">
              <Menu 
                className={`absolute inset-0 w-6 h-6 text-gray-700 transition-all duration-300 ${
                  isMenuOpen ? 'opacity-0 rotate-180' : 'opacity-100 rotate-0'
                }`} 
              />
              <X 
                className={`absolute inset-0 w-6 h-6 text-gray-700 transition-all duration-300 ${
                  isMenuOpen ? 'opacity-100 rotate-0' : 'opacity-0 -rotate-180'
                }`} 
              />
            </div>
          </button>
        </div>

        {/* Mobile Navigation */}
        <div 
          className={`lg:hidden overflow-hidden transition-all duration-500 ${
            isMenuOpen ? 'max-h-96 opacity-100' : 'max-h-0 opacity-0'
          }`}
        >
          <nav className="py-6 space-y-2">
            {menuItems.map((item, index) => (
              <button
                key={item.id}
                onClick={() => scrollToSection(item.href)}
                className={`w-full text-left px-6 py-4 rounded-2xl font-medium transition-all duration-300 transform ${
                  activeSection === item.id
                    ? 'gradient-bg-primary text-white shadow-lg'
                    : 'text-gray-700 hover:bg-gray-100'
                } animate-slide-in-left`}
                style={{ animationDelay: `${index * 0.1}s` }}
              >
                {item.label}
              </button>
            ))}
            
            {/* Mobile CTA */}
            <div className="pt-4">
              <button
                onClick={() => scrollToSection('#contato')}
                className="w-full btn-modern text-white font-semibold animate-slide-in-left"
                style={{ animationDelay: `${menuItems.length * 0.1}s` }}
              >
                Come√ßar Agora
              </button>
            </div>
          </nav>
        </div>
      </div>

      {/* Mobile Menu Overlay */}
      {isMenuOpen && (
        <div 
          className="lg:hidden fixed inset-0 bg-black bg-opacity-20 backdrop-blur-sm -z-10"
          onClick={() => setIsMenuOpen(false)}
        />
      )}
    </header>
  );
}