'use client';

import React, { useState, useEffect } from 'react';
import { Check, CreditCard, Shield, Clock, Users, Star, ArrowRight, ArrowLeft, Lock, Gift } from 'lucide-react';

interface FormData {
  name: string;
  email: string;
  phone: string;
  cpf: string;
  cardNumber: string;
  expiryDate: string;
  cvv: string;
  cardName: string;
}

export default function PaymentSection() {
  const [currentStep, setCurrentStep] = useState(1);
  const [isLoading, setIsLoading] = useState(false);
  const [paymentCompleted, setPaymentCompleted] = useState(false);
  const [formData, setFormData] = useState<FormData>({
    name: '',
    email: '',
    phone: '',
    cpf: '',
    cardNumber: '',
    expiryDate: '',
    cvv: '',
    cardName: ''
  });

  const steps = [
    { id: 1, title: 'Dados Pessoais', icon: Users },
    { id: 2, title: 'Pagamento', icon: CreditCard },
    { id: 3, title: 'Confirma√ß√£o', icon: Check }
  ];

  const benefits = [
    'Treinos 100% personalizados',
    'Acompanhamento nutricional',
    'Suporte 24/7 via WhatsApp',
    'Acesso ao app exclusivo',
    'Garantia de 30 dias',
    'Resultados em 90 dias'
  ];

  useEffect(() => {
    const completed = localStorage.getItem('paymentCompleted');
    if (completed === 'true') {
      setPaymentCompleted(true);
    }
  }, []);

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
  };

  const formatCardNumber = (value: string) => {
    return value.replace(/\s/g, '').replace(/(.{4})/g, '$1 ').trim();
  };

  const formatCPF = (value: string) => {
    return value.replace(/\D/g, '').replace(/(\d{3})(\d{3})(\d{3})(\d{2})/, '$1.$2.$3-$4');
  };

  const formatPhone = (value: string) => {
    return value.replace(/\D/g, '').replace(/(\d{2})(\d{5})(\d{4})/, '($1) $2-$3');
  };

  const nextStep = () => {
    if (currentStep < 3) {
      setCurrentStep(currentStep + 1);
    }
  };

  const prevStep = () => {
    if (currentStep > 1) {
      setCurrentStep(currentStep - 1);
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsLoading(true);

    try {
      // Simular integra√ß√£o com Infinity Pay
      await new Promise(resolve => setTimeout(resolve, 3000));
      
      localStorage.setItem('paymentCompleted', 'true');
      setPaymentCompleted(true);
      setCurrentStep(3);
    } catch (error) {
      console.error('Erro no pagamento:', error);
    } finally {
      setIsLoading(false);
    }
  };

  const openWhatsApp = () => {
    const whatsappUrl = `https://wa.me/5511999999999?text=${encodeURIComponent('Ol√°! Acabei de realizar o pagamento e gostaria de come√ßar minha transforma√ß√£o!')}`;
    window.open(whatsappUrl, '_blank');
  };

  if (paymentCompleted) {
    return (
      <section id="contato" className="py-20 bg-gradient-to-br from-green-50 to-emerald-100">
        <div className="container mx-auto px-4">
          <div className="max-w-2xl mx-auto text-center">
            <div className="w-24 h-24 bg-green-500 rounded-full flex items-center justify-center mx-auto mb-8 animate-scale-in">
              <Check className="w-12 h-12 text-white" />
            </div>
            
            <h2 className="text-4xl font-bold text-gray-900 mb-4">
              Pagamento Confirmado! üéâ
            </h2>
            
            <p className="text-xl text-gray-600 mb-8">
              Sua jornada de transforma√ß√£o come√ßa agora! Clique no bot√£o abaixo para acessar o WhatsApp e receber suas primeiras orienta√ß√µes.
            </p>
            
            <button
              onClick={openWhatsApp}
              className="btn-modern text-white text-lg px-10 py-5 group"
            >
              <span>Acessar WhatsApp</span>
              <ArrowRight className="w-5 h-5 ml-2 group-hover:translate-x-1 transition-transform" />
            </button>
          </div>
        </div>
      </section>
    );
  }

  return (
    <section id="contato" className="py-20 bg-gradient-to-br from-gray-50 to-white">
      <div className="container mx-auto px-4">
        <div className="max-w-6xl mx-auto">
          
          {/* Header */}
          <div className="text-center mb-16">
            <div className="inline-flex items-center space-x-2 bg-orange-100 rounded-full px-6 py-3 mb-6">
              <Gift className="w-5 h-5 text-orange-600" />
              <span className="text-orange-600 font-semibold">Oferta Especial - Apenas R$ 150</span>
            </div>
            
            <h2 className="text-hero font-orbitron text-gray-900 mb-6">
              Comece Sua <span className="gradient-text">Transforma√ß√£o</span>
            </h2>
            
            <p className="text-xl text-gray-600 max-w-3xl mx-auto">
              Investimento √∫nico de R$ 150 para ter acesso completo ao m√©todo que j√° transformou mais de 500 pessoas
            </p>
          </div>

          <div className="grid lg:grid-cols-12 gap-12">
            
            {/* Left Side - Benefits */}
            <div className="lg:col-span-5 space-y-8">
              
              {/* Price Card */}
              <div className="card-modern p-8 text-center">
                <div className="mb-6">
                  <div className="text-sm text-gray-500 line-through">De R$ 300</div>
                  <div className="text-5xl font-bold gradient-text mb-2">R$ 150</div>
                  <div className="text-gray-600">Pagamento √∫nico</div>
                </div>
                
                <div className="flex items-center justify-center space-x-2 text-green-600 mb-6">
                  <Shield className="w-5 h-5" />
                  <span className="font-semibold">Garantia de 30 dias</span>
                </div>
              </div>

              {/* Benefits List */}
              <div className="card-modern p-8">
                <h3 className="text-2xl font-bold text-gray-900 mb-6">O que est√° incluso:</h3>
                <div className="space-y-4">
                  {benefits.map((benefit, index) => (
                    <div key={index} className="flex items-center space-x-3">
                      <div className="w-6 h-6 gradient-bg-primary rounded-full flex items-center justify-center flex-shrink-0">
                        <Check className="w-4 h-4 text-white" />
                      </div>
                      <span className="text-gray-700">{benefit}</span>
                    </div>
                  ))}
                </div>
              </div>

              {/* Security */}
              <div className="card-glass p-6">
                <div className="flex items-center space-x-3 mb-4">
                  <Lock className="w-6 h-6 text-green-600" />
                  <span className="font-semibold text-gray-900">Pagamento 100% Seguro</span>
                </div>
                <p className="text-sm text-gray-600">
                  Seus dados s√£o protegidos com criptografia SSL e processados pela Infinity Pay, 
                  uma das plataformas de pagamento mais seguras do Brasil.
                </p>
              </div>
            </div>

            {/* Right Side - Checkout Form */}
            <div className="lg:col-span-7">
              <div className="card-modern p-8">
                
                {/* Steps Indicator */}
                <div className="flex items-center justify-between mb-8">
                  {steps.map((step, index) => (
                    <div key={step.id} className="flex items-center">
                      <div className={`w-12 h-12 rounded-full flex items-center justify-center border-2 transition-all ${
                        currentStep >= step.id 
                          ? 'gradient-bg-primary border-orange-500 text-white' 
                          : 'border-gray-300 text-gray-400'
                      }`}>
                        {React.createElement(step.icon, { className: "w-6 h-6" })}
                      </div>
                      <div className="ml-3 hidden sm:block">
                        <div className={`text-sm font-semibold ${
                          currentStep >= step.id ? 'text-orange-600' : 'text-gray-400'
                        }`}>
                          {step.title}
                        </div>
                      </div>
                      {index < steps.length - 1 && (
                        <div className={`w-16 h-1 mx-4 rounded ${
                          currentStep > step.id ? 'gradient-bg-primary' : 'bg-gray-200'
                        }`}></div>
                      )}
                    </div>
                  ))}
                </div>

                <form onSubmit={handleSubmit}>
                  
                  {/* Step 1: Personal Data */}
                  {currentStep === 1 && (
                    <div className="space-y-6 animate-fade-in-up">
                      <h3 className="text-2xl font-bold text-gray-900 mb-6">Dados Pessoais</h3>
                      
                      <div className="grid md:grid-cols-2 gap-6">
                        <div>
                          <label className="block text-sm font-semibold text-gray-700 mb-2">
                            Nome Completo *
                          </label>
                          <input
                            type="text"
                            name="name"
                            value={formData.name}
                            onChange={handleInputChange}
                            className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-orange-500 focus:border-transparent transition-all"
                            placeholder="Seu nome completo"
                            required
                          />
                        </div>
                        
                        <div>
                          <label className="block text-sm font-semibold text-gray-700 mb-2">
                            CPF *
                          </label>
                          <input
                            type="text"
                            name="cpf"
                            value={formatCPF(formData.cpf)}
                            onChange={(e) => handleInputChange({...e, target: {...e.target, value: e.target.value.replace(/\D/g, '')}})}
                            className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-orange-500 focus:border-transparent transition-all"
                            placeholder="000.000.000-00"
                            maxLength={14}
                            required
                          />
                        </div>
                      </div>

                      <div className="grid md:grid-cols-2 gap-6">
                        <div>
                          <label className="block text-sm font-semibold text-gray-700 mb-2">
                            E-mail *
                          </label>
                          <input
                            type="email"
                            name="email"
                            value={formData.email}
                            onChange={handleInputChange}
                            className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-orange-500 focus:border-transparent transition-all"
                            placeholder="seu@email.com"
                            required
                          />
                        </div>
                        
                        <div>
                          <label className="block text-sm font-semibold text-gray-700 mb-2">
                            Telefone *
                          </label>
                          <input
                            type="tel"
                            name="phone"
                            value={formatPhone(formData.phone)}
                            onChange={(e) => handleInputChange({...e, target: {...e.target, value: e.target.value.replace(/\D/g, '')}})}
                            className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-orange-500 focus:border-transparent transition-all"
                            placeholder="(11) 99999-9999"
                            maxLength={15}
                            required
                          />
                        </div>
                      </div>

                      <div className="flex justify-end">
                        <button
                          type="button"
                          onClick={nextStep}
                          className="btn-modern text-white px-8 py-3 group"
                          disabled={!formData.name || !formData.email || !formData.phone || !formData.cpf}
                        >
                          <span>Continuar</span>
                          <ArrowRight className="w-5 h-5 ml-2 group-hover:translate-x-1 transition-transform" />
                        </button>
                      </div>
                    </div>
                  )}

                  {/* Step 2: Payment */}
                  {currentStep === 2 && (
                    <div className="space-y-6 animate-fade-in-up">
                      <h3 className="text-2xl font-bold text-gray-900 mb-6">Dados do Cart√£o</h3>
                      
                      <div>
                        <label className="block text-sm font-semibold text-gray-700 mb-2">
                          N√∫mero do Cart√£o *
                        </label>
                        <input
                          type="text"
                          name="cardNumber"
                          value={formatCardNumber(formData.cardNumber)}
                          onChange={(e) => handleInputChange({...e, target: {...e.target, value: e.target.value.replace(/\s/g, '')}})}
                          className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-orange-500 focus:border-transparent transition-all"
                          placeholder="0000 0000 0000 0000"
                          maxLength={19}
                          required
                        />
                      </div>

                      <div>
                        <label className="block text-sm font-semibold text-gray-700 mb-2">
                          Nome no Cart√£o *
                        </label>
                        <input
                          type="text"
                          name="cardName"
                          value={formData.cardName}
                          onChange={handleInputChange}
                          className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-orange-500 focus:border-transparent transition-all"
                          placeholder="Nome como est√° no cart√£o"
                          required
                        />
                      </div>

                      <div className="grid grid-cols-2 gap-6">
                        <div>
                          <label className="block text-sm font-semibold text-gray-700 mb-2">
                            Validade *
                          </label>
                          <input
                            type="text"
                            name="expiryDate"
                            value={formData.expiryDate}
                            onChange={(e) => {
                              let value = e.target.value.replace(/\D/g, '');
                              if (value.length >= 2) {
                                value = value.substring(0, 2) + '/' + value.substring(2, 4);
                              }
                              handleInputChange({...e, target: {...e.target, value}});
                            }}
                            className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-orange-500 focus:border-transparent transition-all"
                            placeholder="MM/AA"
                            maxLength={5}
                            required
                          />
                        </div>
                        
                        <div>
                          <label className="block text-sm font-semibold text-gray-700 mb-2">
                            CVV *
                          </label>
                          <input
                            type="text"
                            name="cvv"
                            value={formData.cvv}
                            onChange={(e) => handleInputChange({...e, target: {...e.target, value: e.target.value.replace(/\D/g, '')}})}
                            className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-orange-500 focus:border-transparent transition-all"
                            placeholder="000"
                            maxLength={4}
                            required
                          />
                        </div>
                      </div>

                      <div className="flex justify-between">
                        <button
                          type="button"
                          onClick={prevStep}
                          className="btn-outline-modern px-8 py-3 group"
                        >
                          <ArrowLeft className="w-5 h-5 mr-2 group-hover:-translate-x-1 transition-transform" />
                          <span>Voltar</span>
                        </button>
                        
                        <button
                          type="submit"
                          className="btn-modern text-white px-8 py-3 group"
                          disabled={isLoading || !formData.cardNumber || !formData.cardName || !formData.expiryDate || !formData.cvv}
                        >
                          {isLoading ? (
                            <div className="flex items-center">
                              <div className="w-5 h-5 border-2 border-white border-t-transparent rounded-full animate-spin mr-2"></div>
                              <span>Processando...</span>
                            </div>
                          ) : (
                            <>
                              <span>Finalizar Pagamento</span>
                              <Lock className="w-5 h-5 ml-2" />
                            </>
                          )}
                        </button>
                      </div>
                    </div>
                  )}
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}